.App {
  text-align: center;
}

.content-container {
  padding: 0 5%;
  margin-top: 64px;
  margin-bottom: 60px;
  background: #282a36;
  color: white !important;
}

.avatar-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.avatar {
  flex-grow: 0;
  margin: 20px;
}
.avatar:hover {
  opacity: 0.9;
  cursor: pointer;
}

/* For tablets: */
@media only screen and (min-width: 600px) {
  .content-container {
    padding: 0 10%;
  }
}

/* For desktop: */
@media only screen and (min-width: 768px) {
  .content-container {
    padding: 0 20%;
  }
}

/* For large screen: */
@media only screen and (min-width: 1000px) {
  .content-container {
    padding: 0 30%;
  }
}
